 <template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel  grey lighten-4 black-text  center">
                        <h3 class="">Register</h3>
                        <form>
                            <div class="input-field">
                                <i class="material-icons prefix">email</i>
                                <input type="text" class="black-text" id="email" v-model="email">
                                <label  for="email">Email</label>
                            </div>
                            <div class="input-field">
                                <i class="material-icons prefix">lock</i>
                                <input  type="text" class="black-text" id="password" v-model="password">
                                <label for="password">Password</label>
                            </div>
                            <button v-on:click="register"  class="btn btn-large grey lighten-4 blue-text">Submit</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase';
 export default {
    name: 'register',
    data: function() {
        return {
            email: '',
            password: ''

        }
    },
    methods: {
        register: function(e){
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
            .then(user => {
                console.log(user.email)
                console.log(user.password)
                alert(`Account created for ${user.email}`);
                
                this.$router.push('/');
            },
            err => {
                alert(err.message);
            })
            e.preventDefault();
        }
    }
}
</script>
<style scoped>
.login{
    background: #274d8a;
    color: #274d8a !important;
}
 .input-field input[type=text]:focus {
     border-bottom: 1px solid greenyellow;
     box-shadow: 0 1px 0 0 greenyellow;
   }
    .input-field .prefix.active {
     color: greenyellow;
   }
   .btn{
       font-weight: bold;
       color: #274d8a !important;
   }
</style>





