<template>
    <div id="new-employee">   
        <div class="row editForm">
             <h4>New Employee</h4>
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="employee_id"
                          required>
                         <label>Employee ID</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name"
                          required>
                         <label>Name</label>
                    </div>
                </div>

                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="dept"
                          required>
                         <label>Department</label>
                    </div>
                </div>

                <div class="row"> 
                    <div class="input-field col s12">
                        <input type="text" v-model="position"
                          required>
                         <label>Position</label>
                    </div>
                </div>
                <router-link to="/" class="btn red">
                <i class="fa fa-chevron-circle-left"></i>
                </router-link>
                <div class="submit">
                    <button type="submit" class="btn">Submit</button>
                </div>
            
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-employee',
    data () {
        return{
             employee_id: null,
            name: null,
            dept: null,
            position: null
        }
    },
    methods: {
        saveEmployee() {
            db.collection('employees').add({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
            })
            .then(docRef => 
                this.$router.push('/')
            )
            .catch(error => console.log(err))
        }
    }
}
</script>

<style scoped>
.submit{
    float: right;
}
.editForm{
    background-color: rgb(243, 237, 227);
    border-radius: 5px;
    padding: 5px;
    box-shadow: 0px 2px;
}
</style>



