<template>
<div>
 <nav class="nav-extended">
     <div class="nav-wrapper lightgreen">    
             <router-link to="/"
             class="brand-logo"> 
             </router-link>
             <ul id="nav-mobile" class="right">
                 <li v-if="isLoggedIn"><span class="email black-text">{{currentUser}}</span></li>
                 <li v-if="isLoggedIn" ><router-link to="/">Dashboard</router-link></li>
                  <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
                   <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
                   <li v-if="isLoggedIn"> <button v-on:click="logout" class="btn red">Logout</button></li>
             </ul>
         </div>
             <div class="nav-content">
      <span class="nav-title"> <router-link to="/"
             class="brand-title"><img src="../assets/nav-title.png" alt=""> </router-link></span>
      <a class="btn-floating btn-large halfway-fab waves-effect waves-light red">
        <i class="material-icons">star</i>
      </a>
    </div>
 </nav>

 </div>
</template>

<style scoped>
.nav-wrapper{
    
    color: gold;
}
nav{
    background-color: #274d8a;
    margin-bottom: 10px;
}
.email{
    padding-right: 10px;
    color: blanchedalmond !important;
    font-weight: bold;
}
.nav-title{
    font-size: 180%;
    font-style: oblique;
}
.btn-floating{
    background-color: #D60000 !important;
}
</style>
<script>
import firebase from 'firebase'
export default {
    data(){
        return {
            isLoggedIn: false,
            currentUser: false
        }
    },
    created(){
        if(firebase.auth().currentUser){
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email;  
        }
    },
    methods: {
        logout: function() {
            firebase.auth().signOut().then(()=>{
                this.$router.push('/login');
            });
        }
    }
    
}
</script>




